                    <template v-if="event.count.day > 0">
                      <b class="text-red">{{ event.count.day }}</b>
                      
                    </template>
                    <template v-else>
                      <b class="text-red">{{ event.count.time }}</b>
                      
                    </template>
                  </span>
                </div>
                <div v-else>
                  {{ event.text }}
                  <span class="d-inline-block">
                    <b class="text-red">
                      <span v-if="event.type === 'live'"></span><span v-else></span>
                    </b>
