  created() {
    let i = 1;

    for (const term in this.list) {
      for (const season in this.list[term]) {
        for (const area in this.list[term][season]) {
          i = 1;

          for (const arr of this.list[term][season][area]) {
            arr.name = `${term}${season}`;
            arr.area = Number(area) + 1;
            arr.stage = i;
            i++;
          }

          this.allItemList = this.allItemList.concat(this.list[term][season][area]);
        }
      }
    }
