  methods: {
    filterItems(store) {
      const _this = this;
      let result = _this.allItemList;

      for (let i = 1; i <= 3; i++) {
        result = ((i) => {
          if (store.localStorageData.selectItemList[`item${i}`].length > 0 && store.localStorageData.selectItemList[`item${i}`].length < _this.filterItemList[`item${i}`].length) {
            const regex = new RegExp(store.localStorageData.selectItemList[`item${i}`].join('|').replace(/\(/g, '\\(').replace(/\)/g, '\\)'))
            return result.filter(arr => regex.test(arr[''][i - 1]));
          } else {
            return result;
          }
        })(i);
      }

      store.setLocalStorage('llllMgr_selectItemList', {
        item1: store.localStorageData.selectItemList.item1,
        item2: store.localStorageData.selectItemList.item2,
        item3: store.localStorageData.selectItemList.item3
      });

      return result;
    },
    searchColor(target) {
      let color;

      for (const itemName in this.colorList) {
        if (target.includes(itemName)) {
          color = this.colorList[itemName];
          break;
        }
      }

      return color;
    },
    selectItem(store, selector, i) {
      if (store.localStorageData.selectItemList[`item${i}`].some((x) => x === selector)) {
        store.localStorageData.selectItemList[`item${i}`] = store.localStorageData.selectItemList[`item${i}`].filter((item) => item !== selector);
      } else {
        store.localStorageData.selectItemList[`item${i}`].push(selector);
      }
    }
  }
};
</script>